/* Slideshow container */

html, body {
  margin: 0;
  padding: 0;
}

.main-container {
  background: url('https://fredbrume.github.io/vado/static/media/burgerfour.25d78ab5.jpg');
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  max-width: 100%;
  background-size: cover;
  background-attachment: fixed;
  overflow: hidden;
  position: relative;
}

header img, header nav, header .btn-order {
  display: inline-block;
  vertical-align: middle;
}

#nav {
  height: auto;
  max-width: 100%;
  line-height: 200%;
}

header .header-logo {
  width: 150px;
  height: 80px;
  margin: 0 10px;
  padding: 8px;
}

header {
  width: 100%;
  z-index: 999;
  position: fixed;
  display: block;
  background-color: #E65100;
}

ul {
  list-style-type: none;
  margin: 0 auto;
  width: 100%;
}

ul li {
  display: inline;
  float: left;
  padding-right: 20px;
}

#nav a:hover {
  background-color: #fff;
  color: #000;
}

#nav a.active {
  background-color: #fff;
  color: #000;
}

#nav a {
  display: block;
  padding: 10px 10px;
  /* background-color: #dee; */
  text-decoration: none;
  font-size: 22px;
  font-weight: bold;
  font-family: arial;
  /* text-shadow: 2px 0 #888888; */
  color: white;
}

.btn-order {
  float: right;
  color: #fff;
  font-size: 25px;
  font-weight: 600;
  margin: 10px 10px;
  font-family: arial;
  letter-spacing: 2px;
  /* text-shadow: 2px 0 #888888; */
}

.btn-order img {
  width: 100px;
  height: 60px;
  transition: all 0.3s ease-out;
}

#toggle {
  position: absolute;
  right: 20px;
  top: 14px;
  z-index: 999;
  width: 35px;
  height: 35px;
  cursor: pointer;
  float: right;
  transition: all 0.3s ease-out;
  visibility: hidden;
}

#toggle .span {
  height: 3px;
  background: #fff;
  margin: 5px auto;
  transition: all 0.3s ease-out;
  backface-visibility: hidden;
}

.resizable-menu-container {
  position: fixed;
  background: #E65100;
  width: 100%;
  height: auto;
  top: 10%;
  z-index: 9;
  transition: all 1s ease-out;
  display: table;
  overflow: hidden;
}

.resizable-menu {
  height: 100vh;
  display: table-cell;
  vertical-align: center;
  padding: 40px;
  margin: 0px;
  transition: all 1s ease-out;
}

.resizable-menu li {
  display: block;
  width: 100%;
  text-align: center;
  padding: 15px 0;
  min-height: 20px;
  font-weight: 200;
  line-height: 1.5;
  cursor: pointer;
  transition: all 0.3s ease-out;
}

.resizable-menu li a {
  color: #fff;
  text-decoration: none;
  font-size: 30px;
  transition: all 1s ease-out;
}

.resizable-menu .active {
  visibility: visible;
  opacity: 0.99;
}

#toggle {
  position: absolute;
  right: 20px;
  top: 14px;
  z-index: 999;
  width: 35px;
  height: 35px;
  cursor: pointer;
  float: right;
  transition: all 0.3s ease-out;
  visibility: hidden;
}

#toggle .span {
  height: 3px;
  margin: 5px auto;
  transition: all 0.3s ease-out;
  backface-visibility: hidden;
}

#toggle.on #bar1 {
  transform: rotate(45deg) translateX(2px) translateY(4px);
}

#toggle.on #bar2 {
  opacity: 0;
}

#toggle.on #bar3 {
  transform: rotate(-45deg) translateX(8px) translateY(-10px);
}

.mySlides img {
